body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.NavBar{
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  bottom: 0;
  display: flex;
  max-width: 1080px;
  width: 100%;
  justify-content: center;
}

.link{
  text-transform: uppercase;
  background-color: #835092;
  color: white;
  font-weight: bold;
  font-size: 16px;
  width: calc((100% / 7) - 20px);
  height: 50px;
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border: 1px solid transparent;
  margin: 0 10px;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.link:hover, .active{
  color: #835092;
  background-color: white;
  border-left-color: #835092; 
  border-right-color: #835092; 
  border-top-color: #835092; 
}

.Footer{
  height: 100px;
  background-color: #C177B5; 
  width: 100%;
}

.content{
  max-width: 1080px;
  margin: 0 auto;
}

.text{
  font-size: 12px;
  padding: 5px 0;
}

.logo{
  height: 30px;
  width: 30px;
  cursor: pointer;
}
.Title{
  text-transform: uppercase;
  font-size: 18px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding-top: 50px;
  padding-left: 50px;
}

.Title-Text{
  padding: 5px 15px;
  border-bottom: 2px solid #2873E8;
}
.SquareContainer{
  display: block;
  width: 100%;
  margin: 30px 0;
}

.SquareContainer-item{
  width: calc(50% - 100px);
  margin: 50px;
  display: inline-block;
  height: 200px;
}
.SquareItem{
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
  border-radius: 15px;
}

.SquareItem-Title{
  text-transform: uppercase;
  color: white;
  position: absolute;
  width: 100%;
  z-index: 0;
  bottom: 0;
}

.SquareItem-Title::before{
  background-color: black;
  opacity: .7;
  z-index: 0;
  content: '';
  width: 100%;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
}

.SquareItem-Span{
  position: relative;
  z-index: 10;
}

.SquareItem-Slide{
  max-height: 0px;
  -webkit-transition: max-height 1s;
  transition: max-height 1s; 
}

.SquareItem-Price-Container{
  width: 50px;
  padding: 10px 25px;
  position: absolute;
  right: -100px;
  -webkit-transition: linear 0.3s;
  transition: linear 0.3s;
}

.SquareItem-Price-Container::before{
  background-color: black;
  opacity: .7;
  z-index: 0;
  content: '';
  width: 100%;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
}

.SquareItem-Price{
  text-transform: uppercase;
  color: white;
  position: relative;
  width: 100%;
  z-index: 0;
  font-weight: bold;
}

.SquareItem-More{
  background-color: #eeeeee;
  height: 100%;
  width: 0;
  overflow: hidden;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.item{
  padding: 1px 0;
  display: block;
  text-align: left;
  position: relative;
  padding-left: 10px;
}

.item:nth-child(even){
  background-color: #f4f4f4;
}

.item-price{
  font-weight: bold;
  right: 10px;
  position: absolute;
}

.item-text{
  white-space: nowrap;
  text-align: left;
  text-transform: capitalize;
}

#display{
  width: 50%;
}

#active{
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

#unactive{
  opacity: 1;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

.SquareItem:hover .SquareItem-Slide{
  max-height: 200px;
}

.SquareItem:hover .SquareItem-Price-Container{
  right: 0;
}

